.wp-block-create-block-blocks-gamestore {
	border: 1px dotted #f00;
}

body {

	font-family: "Urbanist", sans-serif !important;
}



:root{
	--background-default: #fafafa;
	--action-main:rgba(14, 13, 15, 0.64);
	--action-main-svg:rgba(14, 13, 15);
	--divider-main:rgba(14, 13, 15, 0.08);
	--text-primary:rgba(14, 13, 15, 1);
}

.dark-mode-gemstore{
	--background-default: #0e0d0f;
	--action-main:rgba(255, 255, 255, 0.64);
	--action-main-svg:rgba(255, 255, 255);
	--divider-main:rgba(255, 255, 255, 0.08);
	--text-primary:rgba(255, 255, 255, 1);
}
